<template>
  <div class="transverseBar">
    <div ref="transverseBar" class="transverseBar"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  name: "transverse-bar",
  components: {},
  props: {},
  data() {
    return {};
  },
  created() {},
  mounted() {
    // this.init();
  },
  methods: {
    init(yAxisData = [], seriesData = []) {
      let optionData = {
        backgroundColor: "#ffffff",
        tooltip: {
          trigger: "axis",
          show: false,
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          show: false
        },
        grid: {
          left: "3%",
          right: "100",
          bottom: "3%",
          top: "2%",
          containLabel: true
        },
        xAxis: [
          {
            splitLine: {
              show: false
            },
            type: "value",
            show: false
          }
        ],
        yAxis: [
          {
            splitLine: {
              show: false
            },
            axisLine: {
              //y轴
              show: false
            },
            type: "category",
            axisTick: {
              show: false
            },
            inverse: true,
            data: yAxisData,
            axisLabel: {
              color: "#606266",
              fontSize: 14
            }
          }
        ],
        series: [
          {
            name: "标准化",
            type: "bar",
            //barWidth: 25, // 柱子宽度
            barMaxWidth: 36,
            label: {
              show: true,
              position: "right", // 位置
              color: "#ff8000",
              fontSize: 12,
              distance: 15, // 距离
              formatter: "{c}" // 这里是数据展示的时候显示的数据
            }, // 柱子上方的数值
            itemStyle: {
              barBorderRadius: [0, 10, 10, 0], // 圆角（左上、右上、右下、左下）
              color: new echarts.graphic.LinearGradient(
                1,
                0,
                0,
                0,
                [
                  {
                    offset: 0,
                    color: "#51C5FD"
                  },
                  {
                    offset: 1,
                    color: "#005BB1"
                  }
                ],
                false
              ) // 渐变
            },
            data: seriesData
          }
        ]
      };
      this.mychart = echarts.init(this.$refs.transverseBar);
      this.mychart.setOption(optionData);
      let mycharts = echarts.init(this.$refs.transverseBar, true); //监听窗口变化重新加载
      window.addEventListener("resize", mycharts.resize); //监听窗口变化重新加载
      //点击事件
      let $this = this;
      this.mychart.on("click", function(params) {
        window.console.log(params, "transverse-bar");
        $this.$emit("changeBarDetail", params.name);
      });
    }
  }
};
</script>

<style scoped lang="scss">
.transverseBar {
  height: 100%;
}
</style>